<template>
  <div class="container">
    <v-content>
      <v-container fluid fill-height>
        <v-layout align-center justify-center>
          <v-flex xs12 sm12 md12 lg12 v-bind:style="{width : 100+'%'}">
            <img src="../assets/Header.jpg" v-bind:style="{width : 100+'%'}" />
            <img src="../assets/Login.jpg" v-bind:style="{width : 100+'%'}" />
            <v-card class="elevation-12">
              <v-card-text>
                <v-form>
                  <v-btn block color="primary" @click="googleLogin">Getting start with registration!</v-btn>
                </v-form>
              </v-card-text>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
    </v-content>
  </div>

</template>
<style>
  .mt-2 {
    margin-top: 2px;
  }
</style>

<script>
  import firebase from "firebase/app"
  import "firebase/auth"
  import router from '../router'

  export default {
    name: 'login',
    data() {
      return {
        email: '',
        password: '',
        errorMessage: '',
        showError: false
      }
    },
    methods: {
      emailLogin() {
        firebase.auth().signInWithEmailAndPassword(this.email, this.password).then(result => {
          //alert(result)
          router.push('/success')
        }).catch(error => {
          //alert(error);
          this.errorMessage = error.message
          this.showError = true
        })
      },
      googleLogin() {
        const provider = new firebase.auth.GoogleAuthProvider()

        firebase.auth().signInWithPopup(provider).then(result => {
          //alert(result.user)
          router.push('/success')
        }).catch(error => {
          //alert(error)
          this.errorMessage = error.message
          this.showError = true
        })
      }
    }
  }
</script>