import Vue from 'vue'
import Router from 'vue-router'
import Login from './views/Login.vue'
import Success from './views/Success.vue'
import firebase from 'firebase/app'
import USDI from './views/USDI.vue'
import Charts from './views/Charts.vue'
import About from './views/AboutUs.vue'

Vue.use(Router)

export default new Router({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'login',
      component: Login
    },
    {
      path: '/success',
      name: 'success',
      component: Success
    },
    {
      path: '/charts',
      name: 'charts',
      component: Charts
    },
    {
      path: '/usdi',
      name: 'USDI',
      component:USDI
    },
    {
      path: '/about',
      name: 'About US',
      component:About
    }
  ]
})
